import React from 'react';
import ReactDOM from 'react-dom';
import "./../sass/style.scss";
import {
    Router,
    Route,
    Link,
    IndexLink,
    IndexRoute,
    hashHistory
} from 'react-router';
document.addEventListener('DOMContentLoaded',function() {
    class Template extends React.Component {
        render() {
            return <div>
                {this.props.children}
            </div>;
        }
    }
    class Home extends React.Component{
        render(){
            return (
                <div>
                    <Menu/>
                    <div className='homePage'>
                        <h1 className='home'>Witam!</h1>
                    </div>
                </div>
            )
        }
    }
    class AboutMe extends React.Component{
        render(){
            return (
                <div>
                    <Menu/>
                    <Profil/>
                    <Education/>
                    <Tools/>
                    <Interest/>
                </div>
            )
        }
    }
    class Profil extends React.Component{
        render(){
            return (
                <div className='paragraph'>
                    <h1 className='header'>PROFIL</h1>
                    <p className='profil'>
                        Jestem absolwentk Akademii Grniczo-Hutniczej w Krakowie kierunku Inynieria rodowiska.<br></br>
                        W trakcie trwania studiw pojawiy si przedmioty zwizan z programowaniem, co te niezwykle mnie <br></br>
                        zainteresowao i popchno mnie do zapisania si na kurs. Jestem osob, ktra cay czas chce pogbia <br></br>
                        swoj wiedz i rozwija si. Jestem przekonana, e brana IT pozwoli mi na rozwj i realizacj ciekawych <br></br>
                        oraz wymagajcych projektw.
                    </p>
                </div>
            )
        }
    }
    class Education extends React.Component{
        render(){
            return (
                <div className='paragraph'>
                    <h1 className='header'>WYKSZTACENIE</h1>
                    <div className='codersLab'>
                        <img src='images/coders.png' className='codersImg'/>
                        <h2 className='date'>
                            2017 - 2018
                        </h2>
                        <p className='study'>
                            Coders Lab  Szkoa programowania<br></br>
                            Czas trwania: 320 godzin<br></br>
                            Program: zaawansowany HTML i CSS, JavaScript, Sass, RWD, jQuery, ECMAScript6, React.js
                        </p>
                    </div>
                    <div className='educationSection'>
                        <img src='images/agh.png' className='aghImg'/>
                        <h2 className='date'>
                            2016 - 2017
                        </h2>
                        <p className='study'>
                            Akademia Grniczo - Hutnicza im. Stanisawa Staszica w Krakowie<br></br>
                            Wydzia Geodezji Grniczej i Inynierii rodowiska<br></br>
                            Kierunek: Inynieria rodowiska, Studia Magisterskie
                        </p>
                    </div>
                </div>
            )
        }
    }
    class Tools extends React.Component{

        render(){
            const toolsImg=[
                {img:'images/js.png', name:'JavaScript'},
                {img:'images/react.png', name:'React.js'},
                {img:'images/jQuery.png', name:'jQuery'},
                {img:'images/html.png', name:'HTML'},
                {img:'images/sass.svg', name:'SASS'},
                {img:'images/css.png', name:'CSS'},
                {img:'images/webpack.png', name:'Webpack'},
                {img:'images/github.svg', name:'Github'},
                {img:'images/autocad.png', name:'AutoCAD'},
            ];
            const tools=toolsImg.map((i,index)=>{
                return (
                    <img src={i.img} key={index} className='toolsImg'/>
                )
            });
            return (
                <div className='paragraph'>
                    <h1 className='header'>NARRZDZIA/TECHNOLOGIE</h1>
                    <div className="toolsBox">
                        {tools}
                    </div>
                </div>
            )
        }
    }
    class Interest extends React.Component{
        render(){
            const interestImg=[
                {img:'images/f1.png'},
                {img:'images/volley.png'},
                {img:'images/football.svg'},
                {img:'images/ski.png'},
                {img:'images/dance.png'}
            ];
            const interest=interestImg.map((i,index)=>{
                return (
                    <img src={i.img} key={index} className='interestImg'/>
                )
            });
            return(
                <div className='paragraph'>
                    <h1 className='header'>ZAINTERESOWANIA</h1>
                    <div className="interestBox">
                        {interest}
                    </div>
                </div>
            )
        }
    }


    class Projects extends React.Component{
        state={
            img1:'none',
            img2:'none',
            img3:'none'
        };
        handleMouseEnter=()=>{
            this.setState({
                img1:this.state.img1==='none'?'block':'none'
            })
        };

        handleMouseEnter1=()=>{
            this.setState({
                img2:this.state.img2==='none'?'block':'none'
            })
        };

        handleMouseEnter2=()=>{
            this.setState({
                img3:this.state.img3==='none'?'block':'none'
            })
        };

        render(){

            return (
                <div>
                    <Menu/>
                    <div className='projects'>
                        <a href='https://katherina611.github.io/Warsztat-HTML/' onMouseEnter={this.handleMouseEnter} >SIT ON CHAIR</a>
                        <img className='projectsImg' src='images/sit.png' style={{display:this.state.img1}}/>
                        <a href='https://katherina611.github.io/Projekt-koncowy/' onMouseEnter={this.handleMouseEnter1}>DESIGN YOUR HOME</a>
                        <img className='projectsImg' src='images/projekt_kocowy.png' style={{display:this.state.img2}}/>
                        <a href='https://katherina611.github.io/Warsztat-Calculator/' onMouseEnter={this.handleMouseEnter2}>CALCULATOR</a>
                        <img className='projectsImg' src='images/kalkulator.png' style={{display:this.state.img3}}/>
                    </div>
                </div>
            )

        }
    }
    class Contact extends React.Component{
        state={
            img:'block',
            par:'none'
        };
        handleMouseEnter=()=>{
            if(this.state.par==='none'){
                this.setState({
                    par:'block'
                })
            }
        };

        handleMouseLeave=()=>{
            if(this.state.par==='block'){
                this.setState({
                    par:'none'
                })
            }
        };

        render(){

            const imgTel=[
                {img:'images/mobile.svg', data:'795 009 790'},
                {img:'images/gmail.png', data:'katarzynag0609@gmail.com'}
            ];
            const images=[
                {img:'images/github.svg', url:'https://github.com/Katherina611'},
                {img:'images/linkedin.png', url:'https://www.linkedin.com/in/katarzyna-gacek-4a5685143/'},
                {img:'images/fb.png', url:'https://web.facebook.com/kasia.gacek.33'},
            ];

            const social=images.map((i,index)=>{
                return (
                    <a href={i.url} key={index}>
                        <img className='imgContact' key={index} src={i.img}/>
                    </a>
                )
            });

            const contact=imgTel.map((i,index)=>{
                return (
                    <div className='boxContact'>
                        <img className='imgContact' key={index} src={i.img} style={{display:this.state.img}} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}/>
                        <p style={{display:this.state.par}}>{i.data}</p>
                    </div>
                )
            });
            return (
                <div>
                    <Menu/>
                    <div className='boxContact'>
                        {social}
                        {contact}
                    </div>
                </div>
            )
        }
    }
    class Menu extends React.Component{
        render(){
            const style={
                borderBottom:'2px solid grey'
            };
            return(
                <div>
                    <ul className='links'>
                        <li><IndexLink to="/" className='indexLink' activeStyle={style}>Strona gwna</IndexLink></li>
                        <li><IndexLink to="/aboutMe" className='indexLink' activeStyle={style}>O mnie</IndexLink></li>
                        <li><IndexLink to="/projects" className='indexLink' activeStyle={style}>Projekty</IndexLink></li>
                        <li><IndexLink to="/contact" className='indexLink' activeStyle={style}>Kontakt</IndexLink></li>
                    </ul>
                    {this.props.children}
                </div>
            )
        }
    }
    class App extends React.Component{
        render(){
            return (
                <Router history={hashHistory}>
                    <Route path='/' component={Template}>
                        <IndexRoute component={Home} />
                        <Route path='/aboutMe' component={AboutMe} />
                        <Route path='/projects' component={Projects} />
                        <Route path='/contact' component={Contact} />
                    </Route>
                </Router>
            )
        }
    }
    ReactDOM.render(
        <App/>,
        document.getElementById('app')
    )
});


